<template>
    <div>
        <div id="myTable" class="myTable">
            <div class="table-btn">
                <button class="my-btn btn-active btn3" @click="save">保存设置</button>
            </div>
            <div id="tableContent">
                <router-view ref='child'></router-view>
            </div>
            <div class="controls-btn">
                <div class="push-controls">
                    <button :class="leftClass" @click="toLeft()" :disabled="leftClass.disable"> < </button>
                    <!-- <a :class="rightClass" @click="toRight()"> > </a> -->
                </div>
                <div class="btn-content" @mousedown="mouseMove($event)">
                    <router-link v-for="(btn,index) in btnData" :key="index" :to="{path:btn.name}"  @click.native="getTable(btn.name,btn.type)" class="con-dgm-btn" v-show="btn.isShow"> {{btn.textBtn}}</router-link>
                </div>
                <div class="push-controls push-right">
                    <!-- <a :class="leftClass" @click="toLeft()"> < </a> -->
                    <button :class="rightClass" @click="toRight()" :disabled="rightClass.disable"> > </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import getApi from 'network/getApi'
    export default {
        name: "MonitorUt",
        data(){
            return{
                title:'DGU2600设备监听',
                ins:0,//切换按钮active
                name:'Monitor2600',
                btnData: [//按钮初始化
                    {type:1,textBtn:"DGU2600设备监听",name:'Monitor2600',isShow: true},
                    {type:2,textBtn:"E821设备监听",name:'Monitor821',isShow: true},
                    {type:3,textBtn:"DguV1设备监听",name:'MonitorDguV1',isShow: true},
                    {type:4,textBtn:"Zigbee监听",name:'Zigbee',isShow: true},
                    {type:5,textBtn:"WHD监听",name:'Monitorwhd',isShow: true},
                    {type:6,textBtn:"TMU设备监听",name:'MonitorTms',isShow: true},
                    {type:7,textBtn:"油气井设备监听",name:'MonitorOil',isShow: true},
                    {type:8,textBtn:"WL9100设备监听",name:'MonitorWL9100',isShow: true},
                ],
                leftClass: {
                    "left-btn": true,
                    "active": false,
                    "disable" : false,
                },
                rightClass: {
                    "right-btn": true,
                    "active": false,
                    "disable" : false,
                },
                obj:{
                    name:'',
                    chName:'',
                    flag:false,
                }
            }
        },
        watch:{
            '$store.state.customInfo':{
                handler(value, oldValue) {
                    if (value) {
                        if(value.monitorBtn){
                           this.btnData = value.monitorBtn;
                        }
                    }
                },
                deep: true,
                immediate: true
            }
        },
        mounted(){
            this.name = this.$route.name; //刷新的时候让button 的active 正确
            this.statePage(this.name);
            this.setBtnContentPosition();
        },
        methods: {
            getTable(name) { //点击下标签的时候改变active，改变路由，刷新之后路由不变，active不变，点击左边测点设置 active会有问题
                this.name = name;
                this.statePage(name);
            },
            save() {
                let type = this.$refs.child.type;
                if(type==1){//DGU2600设备监听
                    let params = null;
                    params = {
                        monitor: this.$refs.child.isOpen.selectVal,
                        port: this.$refs.child.monitorList[2].value,
                        listenCount: this.$refs.child.monitorList[0].value,
                        host: this.$refs.child.monitorList[1].value,
                        outTime: this.$refs.child.monitorList[3].value
                    };
                    if(!this.isIP(params.host)){
                        swal({
                            title: "提示",
                            text: '请输入规范的ip',
                            button: "确认",
                        });
                        return false;
                    }
                    else if(!this.isPort(params.port)){
                        swal({
                            title: "提示",
                            text: '请输入规范的端口号',
                            button: "确认",
                        });
                        return false;
                    }
                    else if(!this.isIntNum(params.listenCount)){
                        swal({
                            title: "提示",
                            text: '监听个数请输入数字',
                            button: "确认",
                        });
                        return false;
                    }
                    else if(!this.isIntNum(params.outTime)){
                        swal({
                            title: "提示",
                            text: '请求数据时间请输入数字',
                            button: "确认",
                        });
                        return false;
                    }
                    else{
                        getApi.setBoltConfig(params)
                            .then(res => {
                                if (res.msg == 0) {
                                    swal({
                                        title: "提示",
                                        text: '保存成功',
                                        button: "确认",
                                    })
                                } else if (res.msg == 1) {
                                    console.log('参数为空！');
                                    swal({
                                        title: "提示",
                                        text: '保存失败',
                                        button: "确认",
                                    });
                                } else if (res.msg == 2) {
                                    console.log('后台运行错误！');
                                    swal({
                                        title: "提示",
                                        text: '保存失败',
                                        button: "确认",
                                    });
                                } else if (res.msg == 3) {
                                    console.log('获取json数据时为空！');
                                    swal({
                                        title: "提示",
                                        text: '保存失败',
                                        button: "确认",
                                    });
                                }
                            })
                    }
                }
                else{
                    let params = null;
                    switch (parseInt(type)){
                        case 2://E821设备监听
                            params = {
                                type: 2,
                                config:{
                                    monitor: this.$refs.child.isOpen.selectVal,
                                    port: this.$refs.child.monitorList[0].value,
                                    outTime: this.$refs.child.monitorList[1].value,
                                    maxConnect:this.$refs.child.monitorList[2].value,//最大连接数
                                }
                            };
                            break;
                        case 3://DguV1设备监听
                            params = {
                                type: 3,
                                config:{
                                    monitor: this.$refs.child.isOpen.selectVal,
                                    port: this.$refs.child.monitorList[0].value,
                                    listenOutTime: this.$refs.child.monitorList[1].value,//监听超时
                                    connectOutTime: this.$refs.child.monitorList[2].value,//连接超时
                                    transferOutTime: this.$refs.child.monitorList[3].value,//传输超时
                                    transferCache: this.$refs.child.monitorList[4].value,//传输缓存
                                }
                            };
                            break;
                        case 4://Zigbee监听
                            params = {
                                type: 4,
                                config:{
                                    monitor: this.$refs.child.isOpen.selectVal,
                                    port: this.$refs.child.monitorList[0].value,
                                    maxConnect:this.$refs.child.monitorList[1].value,//最大连接数
                                    packetSize:this.$refs.child.monitorList[2].value,//数据分包大小
                                    singlePacketOutTime:this.$refs.child.monitorList[3].value,//单包超时时间
                                    maxDataSpace:this.$refs.child.monitorList[4].value,//数据最大空间
                                    transferCache:this.$refs.child.monitorList[5].value,//数据传输缓存
                                    recvTimeOut:this.$refs.child.monitorList[6].value,//数据接收超时时间
                                }
                            };
                            break;
                        case 5://WHD监听
                            params = {
                                type: 5,
                                config:{
                                    monitor: this.$refs.child.isOpen.selectVal,
                                    port: this.$refs.child.monitorList[0].value,
                                    outTime: this.$refs.child.monitorList[1].value,
                                    maxConnect:this.$refs.child.monitorList[2].value,//最大连接数
                                }
                            };
                            break;
                        case 6://TMU设备监听
                            params = {
                                type: 6,
                                config:{
                                    monitor: this.$refs.child.isOpen.selectVal,
                                    port: this.$refs.child.monitorList[0].value,
                                    listenOutTime: this.$refs.child.monitorList[1].value,
                                    maxConnect:this.$refs.child.monitorList[2].value,//最大连接数
                                }
                            };
                            break;
                        case 7://油气井设备监听
                            params = {
                                type: 7,
                                config:{
                                    monitor: this.$refs.child.isOpen.selectVal,
                                    port: this.$refs.child.monitorList[0].value,
                                    outTime: this.$refs.child.monitorList[1].value,
                                    maxConnect:this.$refs.child.monitorList[2].value,//最大连接数
                                }
                            };
                            break;
                        case 8://WL9100设备监听
                            params = {
                                type: 8,
                                config:{
                                    monitor: this.$refs.child.isOpen.selectVal,
                                    port: this.$refs.child.monitorList[0].value,
                                    outTime: this.$refs.child.monitorList[1].value,
                                    maxConnect:this.$refs.child.monitorList[2].value,//最大连接数
                                }
                            };
                            break;
                    }

                    let obj = params.config;
                    for(let key in obj){
                        if(key === 'port' && !this.isPort(obj.port)){
                            swal({
                                title: "提示",
                                text: '请输入规范的端口号',
                                button: "确认",
                            });
                            return false;
                        }
                        else if(key !== 'port' && key !== 'monitor' && !this.isIntNum(obj[key])){
                            swal({
                                title: "提示",
                                text: '请输入非负整数',
                                button: "确认",
                            });
                            return false;
                        }
                    }
                    getApi.setListenConfig(params)
                        .then(res => {
                            if (res.msg == 0) {
                                swal({
                                    title: "提示",
                                    text: '保存成功',
                                    button: "确认",
                                })
                            } else if (res.msg == 1) {
                                console.log('参数为空！');
                                swal({
                                    title: "提示",
                                    text: '保存失败',
                                    button: "确认",
                                });
                            } else if (res.msg == 2) {
                                console.log('后台运行错误！');
                                swal({
                                    title: "提示",
                                    text: '保存失败',
                                    button: "确认",
                                });
                            } else if (res.msg == 3) {
                                console.log('获取json数据时为空！');
                                swal({
                                    title: "提示",
                                    text: '保存失败',
                                    button: "确认",
                                });
                            }
                        })

                }
            },
            /*ip号校验*/
            isIP(ip) {
                var reSpaceCheck = /^(\d+)\.(\d+)\.(\d+)\.(\d+)$/;
                if (reSpaceCheck.test(ip)) {
                    ip.match(reSpaceCheck);
                    if (RegExp.$1 <= 255 && RegExp.$1 >= 0
                        && RegExp.$2 <= 255 && RegExp.$2 >= 0
                        && RegExp.$3 <= 255 && RegExp.$3 >= 0
                        && RegExp.$4 <= 255 && RegExp.$4 >= 0) {
                        return true;
                    } else {
                        return false;
                    }
                } else {
                    return false;
                }
            },

            /*端口号校验*/
            isPort(str){
                var parten = /^(\d)+$/g;
                if (parten.test(str) && parseInt(str) <= 65535 && parseInt(str) >= 1) {
                    return true;
                } else {
                    return false;
                }

            },
            /**
             * 校验正负正数就返回true
             **/
            isIntNum(val){
                var regPos = /^\d+$/; // 非负整数
                if(regPos.test(val)){
                    return true;
                }else{
                    return false;
                }
            },
            /* 左右拖动 */
            mouseMove(e){
                let odiv = e.target;        //获取目标元素
                //算出鼠标相对元素的位置
                let disX = e.clientX - odiv.offsetLeft
                let maxWidth = document.getElementsByClassName('controls-btn')[0].offsetWidth - 120;
                document.onmousemove = (e)=>{       //鼠标按下并移动的事件
                    //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
                    let left = e.clientX - disX;
                    if(left < 80 && left > - maxWidth){
                        //移动当前元素
                        odiv.style.left = left + 'px';
                    }
                };
                document.onmouseup = (e) => {
                    document.onmousemove = null;
                    document.onmouseup = null;
                };
            },
            toLeft: function () {//向左移动
                let btnContent = $('.btn-content').position();
                let leftVal = btnContent.left;
                if(leftVal<60){
                    this.leftClass={
                        "left-btn": true,
                        "active": true,
                        "disable" : false,
                    };
                    this.rightClass= {
                        "right-btn": true,
                        "active": false,
                    };
                    $('.btn-content').css('left',leftVal+50)
                }
                else{
                    this.leftClass={
                        "left-btn": true,
                        "active": false,
                        "disable" : true,
                    };
                    this.rightClass= {
                        "right-btn": true,
                        "active": false,
                    };
                }
            },
            toRight: function () {//向右移动
                let btnContent = $('.btn-content').position();
                let btnConWidth = $('.btn-content').width();
                let leftVal = btnContent.left;
                $('.btn-content').css('left',leftVal-50);
                if(leftVal>=100){
                    this.leftClass={
                        "left-btn": true,
                        "active": false,
                        "disable" : true,
                    };
                    this.rightClass= {
                        "right-btn": true,
                        "active": true,
                    };
                }
                else if(Math.abs(leftVal)>btnConWidth-150){
                    this.leftClass={
                        "left-btn": true,
                        "active": false,
                        "disable" : false,
                    };
                    this.rightClass= {
                        "right-btn": true,
                        "active": true,
                        "disable" : true,
                    };
                }
                else{
                    this.leftClass={
                        "left-btn": true,
                        "active": false,
                        // "disable" : false,
                    };
                    this.rightClass= {
                        "right-btn": true,
                        "active": true,
                    };
                }

            },
            setBtnContentPosition(){
                let num;
                let that = this;
                let btnContent = $('.btn-content').position();
                let btnConWidth = $('.btn-content').width();
                let leftVal = btnContent.left;
                this.btnData.forEach((item,index)=>{
                    if(item.name ==that.name ){
                        num = index
                    }
                })
                $('.btn-content').css('left',leftVal-50*num);
                if(leftVal>=60){
                    this.leftClass={
                        "left-btn": true,
                        "active": false,
                        "disable" : true,
                    };
                }
                if(Math.abs(leftVal)>btnConWidth-150){
                    this.rightClass= {
                        "right-btn": true,
                        "active": true,
                        "disable" : true,
                    };
                }
            },
            //页面状态
            statePage(name){
                switch (name) {
                    case 'Monitor2600':
                        this.title = 'DGU2600设备监听';
                        this.obj.chName = "DGU2600设备监听";
                        break;
                    case 'Monitor821':
                        this.title = 'E821设备监听';
                        this.obj.chName = "E821设备监听";
                        break;
                    case 'MonitorDguV1':
                        this.title = 'DguV1设备监听';
                        this.obj.chName = "DguV1设备监听";
                        break;
                    case 'Zigbee':
                        this.title = 'Zigbee监听';
                        this.obj.chName = "Zigbee监听";
                        break;
                    case 'Monitorwhd':
                        this.title = 'WHD监听';
                        this.obj.chName = "WHD监听";
                        break;
                    case 'MonitorTms':
                        this.title = 'TMU监听';
                        this.obj.chName = "TMU监听";
                        break;
                    case 'MonitorOil':
                        this.title = '油气井监听';
                        this.obj.chName = "油气井监听";
                        break;
                    case 'MonitorWL9100':
                        this.title = 'WL9100设备监听';
                        this.obj.chName = "WL9100设备监听";
                        break;
                }
                this.$emit('listenToChildEvent',this.obj);
            }
        }
    }
</script>

<style scoped>
    #myTable{
        height: calc(100% - 40px);
    }
</style>
